.App {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(90deg, rgb(48, 230, 48), rgb(240, 49, 240));
  margin: 0;
  padding: 0;
  color: wheat;

  &::before {
    content: "";
    height: 45%;
    aspect-ratio: 1/1;
    border-radius: 50%;
    position: absolute;
    left: 5%;
    bottom: 15%;
    background: radial-gradient(rgb(4, 236, 101), rgb(4, 104, 26));
    background-repeat: no-repeat;
    box-shadow: 0 0 18rem 18rem rgb(0, 128, 38);
    filter: blur(20px);
    z-index: 10;
  }

  &::after {
    content: "";
    height: 45%;
    aspect-ratio: 1/1;
    border-radius: 50%;
    position: absolute;
    right: 5%;
    top: 15%;
    background: radial-gradient(rgb(236, 4, 236), rgb(104, 4, 104));
    background-repeat: no-repeat;
    box-shadow: 0 0 25rem 6rem purple;
    filter: blur(20px);
    z-index: 10;
  }

  .Mask {
    position: absolute;
    height: 100%;
    width: 100%;
    background-color: #44444455;
    z-index: 100;

    .Container {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 70%;
      max-height: 90%;
      max-width: 60%;
      aspect-ratio: 3/2;
      padding: 1rem;
      background-color: rgba($color: #000000, $alpha: .08);
      backdrop-filter: blur(4px);
      border-radius: 16px;
      display: flex;
      gap: 5px;

      &>section {
        flex: 1;
        position: relative;
        padding: 10px;

        .Prev {
          position: absolute;
          left: 50%;
          right: 50%;
          top: 5%;
          transform: translateX(-50%);
          width: 90%;
          max-height: 90%;
          aspect-ratio: 1/1;
          display: grid;
          place-items: center;
          background-color: rgba($color: #000000, $alpha: .15);
          border-radius: 8px;

          img {
            height: 95%;
            width: 95%;
            max-height: 100%;
            max-width: 100%;
          }

          +.Download {
            position: absolute;
            bottom: 0.8rem;
            right: 0.8rem;
            height: 2.5rem;
            aspect-ratio: 1/1;
            padding: 5px;
            background: rgb(248, 66, 224);
            box-shadow: inset 0 0 20px 5px #74ee7e;
            border-radius: 50%;
          }
        }

      }
      .DataSection {

        label {
          font-size: medium;
        }

        select,input,button {
          height: 1.6rem;
          width: 6rem;
          outline: none;
          border: 0;
          border-radius: 4px;
          padding-left: 4px;
          margin-left: 4px;
        }
        input {
          width: 10rem;
        }
        input[type="checkbox"] {
          height: unset;
          width: unset;
        }
        button {
          height: 2.5rem;
          width: 9rem;
          font-size: medium;
          background-color: rgba(132, 0, 255, 0.849);
          color: wheat;
        }
        textarea {
          border-radius: 4px;
          outline: none;
          border: 0;
          margin: 8px;
        }
      }
    }


  }
}

@media only screen and (max-width: 680px) {
  .App {
    .Mask {
      .Container {
        height: 95%;
        width: 95%;
        aspect-ratio: unset;
        max-width: unset;
        max-height: unset;
      }
    }
  }
}